.ProjectList {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.projectItem {
  width: 28%;
  height: 300px;
  border: 1px solid black;
  padding: 20px;
  margin: 10px auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: relative;
}

.projectTitle {
  font-size: 30px;
  position: absolute;
  width: 100%;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-transition: opacity 1s linear;
  -o-transition: opacity 1s linear;
  transition: opacity 1s linear;
}

.projectItem:hover > .projectTitle {
  opacity: 0;
}

.projectDescription,
.projectLinks {
  opacity: 0;
  -webkit-transition: opacity 1s linear;
  -o-transition: opacity 1s linear;
  transition: opacity 1s linear;
  margin: auto;
}

.projectItem:hover > .projectDescription,
.projectItem:hover > .projectLinks {
  opacity: 1;
}

.projectLinks > div {
  text-decoration: none;
  padding: 5px 0;
}


